@font-face {
  font-family: 'open-sans';
  src: url('./open-sans/open-sans-v26-latin-regular.woff2') format('woff2'),
      url('./open-sans/open-sans-v26-latin-regular.woff') format('woff'),
      url('./open-sans/open-sans-v26-latin-regular.svg') format('svg'),
      url('./open-sans/open-sans-v26-latin-regular.ttf')  format('truetype');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

body {
    background-color: #0b0c0e;
    font-family: 'open-sans'
}
   

   /* Video */
.Video {

  z-index: -10;
  min-height: 100%;
  
}

.main {
  margin-bottom: 20vh;
}

.logoBlanco {
  width: 15vw;
}

.letraPequenya {
  font-size: 9px;
}

.numberSize {
  font-size: calc(2vw + 2.4vmin);
}

.bnb-brand {
  color: #f3ba2f;
}

.btn-metamask {
  background-image: linear-gradient(to bottom, #ee8422, rgba(154, 40, 87, 0.24)),
    url("./logoMetamask.png");
  background-repeat: no-repeat;
  background-size: cover;
  /* background-position: center; */
  height: 50px;
  width: 20%;
  border-radius: 10px;
  font-size: 1.5rem;
  color: #1b2735;
  font-weight: bold;
}

.logoBNB {
  width: 42px;
}

.App {
  text-align: center;
}

.App-buttons {
  /* background-color: #89828f; */
  width: 90vw;
  margin: 0 auto;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #9b0ac8;
  z-index: -10;
}

/** ================================================================
                Botones NavBar
====================================================================*/

.btnHeader {
  border-radius: 35px;
  cursor: text;
  color: white;
  font-weight: bold;
  outline: none;
  width: 15vw;
  padding: 10px 20px;
  transition: background-position 0.8s ease;
}

.btnHeader:hover {
  background-position: 0% 30%;
  box-shadow: 0px 0px 20px 5px rgba(50, 212, 253, 0.849);
}

.connectionMM {
  width: 25vw;
  color: #3a0be1;
  background-size: cover;
  box-shadow: 0px 0px 0px 2px rgba(255, 255, 255, 0.16) inset,
    0px 0px 10px 0px #b191d482;
  background-image: -webkit-gradient(
      linear,
      left top,
      left bottom,
      from(#d1c7dd82),
      to(#b191d482)
    ),
    url("./logoMetamask.png");
  background-image: linear-gradient(to bottom, #d1c7dd82, #b191d482),
    url("./logoMetamask.png");
}

.BNB {
  background-size: cover;
  box-shadow: 0px 0px 0px 2px rgba(255, 255, 255, 0.16) inset,
    0px 0px 10px 0px #e48a3c;
  background-image: -webkit-gradient(
      linear,
      left top,
      left bottom,
      from(#e48a3c),
      to(rgba(222, 135, 61, 0.24))
    ),
    url("https://blog.bitnovo.com/wp-content/uploads/2021/03/Que%CC%81-es-Binance-Coin-BNB-1.jpg");
  background-image: linear-gradient(
      to bottom,
      #e48a3c,
      rgba(222, 135, 61, 0.24)
    ),
    url("https://blog.bitnovo.com/wp-content/uploads/2021/03/Que%CC%81-es-Binance-Coin-BNB-1.jpg");
}

.tops {
  background-image: linear-gradient(to bottom, #bd2be9, rgba(22, 135, 61, 0.24)),
    url("./background-logo.png");
  background-size: cover;
  box-shadow: 0px 0px 0px 2px rgba(255, 255, 255, 0.16) inset,
    0px 0px 10px 0px #bd2be9;
}

/** =================================================================
                Cards Info Precio y Dispo
====================================================================*/

.bnbCard {
  color: #fff;
  border-radius: 30px;
  outline: none;
  box-shadow: 0px 0px 0px 2px rgba(255, 255, 255, 0.16) inset,
    0px 0px 10px 0px #e48a3c;
  background-image: -webkit-gradient(
      linear,
      left top,
      left bottom,
      from(#e48a3c),
      to(rgba(222, 135, 61, 0.24))
    ),
    url("https://blog.bitnovo.com/wp-content/uploads/2021/03/Que%CC%81-es-Binance-Coin-BNB-1.jpg");
  background-image: linear-gradient(
      to bottom,
      #e48a3c,
      rgba(222, 135, 61, 0.24)
    ),
    url("https://blog.bitnovo.com/wp-content/uploads/2021/03/Que%CC%81-es-Binance-Coin-BNB-1.jpg");
  transition: background-position 0.8s ease;
}

.bnbCard:hover {
  background-position: 30% 50%;
  transition: background-position 0.8s ease;
  box-shadow: 0px 0px 0px 2px rgba(255, 255, 255, 0.16) inset,
    0px 0px 30px 0px #e48a3c;
}

.bnbCard b {
  -webkit-text-stroke: 1px #d2cde2;
  font-size: 3rem;
  color: #e99e40;
  font-weight: 700;
}

.topsCard {
  color: #fff;
  border-radius: 30px !important;
  box-shadow: 0px 0px 0px 2px rgba(255, 255, 255, 0.16) inset,
    0px 0px 10px 0px #000000;
  background-image: -webkit-gradient(
      linear,
      left top,
      left bottom,
      from(#20152e),
      to(rgba(86, 202, 139, 0.18))
    ),
    url("./background-logo.png");
  background-image: linear-gradient(
      to bottom,
      #20152e,
      rgba(86, 202, 139, 0.18)
    ),
    url("./background-logo.png");
  transition: background-position 0.8s ease;
}

.topsCard:hover {
  background-position: 30% 50%;
  transition: background-position 0.8s ease;
  box-shadow: 0px 0px 0px 2px rgba(255, 255, 255, 0.16) inset,
    0px 0px 30px 0px #9c76c7;
}

.topsCard b {
  -webkit-text-stroke: 1px #d2cde2;
  font-size: 3.5rem;
  color: #9b0ac8;
  font-weight: 700;
}

.numberSize {
  font-size: calc(2vw + 2.4vmin);
}

/** =============================================================
                Botón compra
=================================================================*/

.buy {
  border: none;
  border-radius: 4px;
  text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.48);
  overflow: hidden;
  padding: 20px 50px 20px 70px;
  margin-bottom: 20px;
  font-size: 20px;
  position: relative;
  color: white;
  outline: none;
  cursor: pointer;
  width: 100%;
  -webkit-transition: background-position 0.7s, box-shadow 0.4s;
  transition: background-position 0.7s, box-shadow 0.4s;
  background-size: 110%;
  font-family: "Oswald", sans-serif;
  box-shadow: 0px 0px 0px 2px rgba(255, 255, 255, 0.16) inset,
    0px 0px 10px 0px #210629;
  background-image: -webkit-gradient(
      linear,
      left top,
      left bottom,
      from(#210629),
      to(rgba(154, 40, 87, 0.24))
    )
    url("");
  /* font-size: calc(1rem + 0.5vh); */
  font-size: 2rem;
  background-image: linear-gradient(to bottom, #210629, rgba(154, 40, 87, 0.24)),
    url("./button-image.jpg");
}

.buy:hover {
  background-position: 0% 30%;
  box-shadow: 0px 0px 0px 2px rgba(255, 255, 255, 0.16) inset,
    0px 2px 5px 5px rgb(248, 234, 248);
}

.buy b {
  color: #3a0be1;
  font-weight: 700;
}

.addToMetaMask:hover {
  box-shadow: 0px 0px 0px 2px rgba(255, 255, 255, 0.16) inset,
    0px 0px 30px 0px #ee8422;
}

/** ============================ BOTÓN INFO ====================================== */

.info {
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  font-size: 1rem;
  border: none;
  color: #0099ff;
}

/** =============== INPUTS  ==========================*/

.form-wrapper {
  background-color: #f6f6f6;
  background-image: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(#f6f6f6),
    to(#eae8e8)
  );
  background-image: -webkit-linear-gradient(top, #f6f6f6, #eae8e8);
  background-image: -moz-linear-gradient(top, #f6f6f6, #eae8e8);
  background-image: -ms-linear-gradient(top, #f6f6f6, #eae8e8);
  background-image: -o-linear-gradient(top, #f6f6f6, #eae8e8);
  background-image: linear-gradient(top, #f6f6f6, #eae8e8);
  border-color: #dedede #bababa #aaa #bababa;
  border-style: solid;
  border-width: 1px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
  -webkit-box-shadow: 0 3px 3px rgba(255, 255, 255, 0.1), 0 3px 0 #bbb,
    0 4px 0 #aaa, 0 5px 5px #444;
  -moz-box-shadow: 0 3px 3px rgba(255, 255, 255, 0.1), 0 3px 0 #bbb,
    0 4px 0 #aaa, 0 5px 5px #444;
  box-shadow: 0 3px 3px rgba(255, 255, 255, 0.1), 0 3px 0 #bbb, 0 4px 0 #aaa,
    0 5px 5px #444;
  margin: 20px auto;
  overflow: hidden;
  padding: 8px;
  width: 100%;
  min-width: 35vw;
}

.form-wrapper .amount {
  border: 1px solid #ccc;
  -webkit-box-shadow: 0 1px 1px #ddd inset, 0 1px 0 #fff;
  -moz-box-shadow: 0 1px 1px #ddd inset, 0 1px 0 #fff;
  box-shadow: 0 1px 1px #ddd inset, 0 1px 0 #fff;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  color: #999;
  float: left;
  font: 28px Lucida Sans, Trebuchet MS, Tahoma, sans-serif;
  height: 50px;
  padding: 10px;
  width: 100%;
}

.form-wrapper .amount:focus {
  border-color: #aaa;
  -webkit-box-shadow: 0 1px 1px #bbb inset;
  -moz-box-shadow: 0 1px 1px #bbb inset;
  box-shadow: 0 1px 1px #bbb inset;
  outline: 0;
}

.form-wrapper .amount:-moz-placeholder,
.form-wrapper .amount:-ms-input-placeholder,
.form-wrapper .amount::-webkit-input-placeholder {
  color: #999;
  font-weight: normal;
}

.form-wrapper #submit {
  background-color: #0483a0;
  background-image: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(#31b2c3),
    to(#0483a0)
  );
  background-image: -webkit-linear-gradient(top, #31b2c3, #0483a0);
  background-image: -moz-linear-gradient(top, #31b2c3, #0483a0);
  background-image: -ms-linear-gradient(top, #31b2c3, #0483a0);
  background-image: -o-linear-gradient(top, #31b2c3, #0483a0);
  background-image: linear-gradient(top, #31b2c3, #0483a0);
  border: 1px solid #00748f;
  -moz-border-radius: 3px;
  -webkit-border-radius: 3px;
  border-radius: 3px;
  -webkit-box-shadow: 0 1px 0 rgba(255, 255, 255, 0.3) inset, 0 1px 0 #fff;
  -moz-box-shadow: 0 1px 0 rgba(255, 255, 255, 0.3) inset, 0 1px 0 #fff;
  box-shadow: 0 1px 0 rgba(255, 255, 255, 0.3) inset, 0 1px 0 #fff;
  color: #fafafa;
  cursor: pointer;
  height: 42px;
  float: right;
  font: 15px Arial, Helvetica;
  padding: 0;
  text-transform: uppercase;
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.3);
  width: 100px;
}

.form-wrapper #submit:hover,
.form-wrapper #submit:focus {
  background-color: #31b2c3;
  background-image: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(#0483a0),
    to(#31b2c3)
  );
  background-image: -webkit-linear-gradient(top, #0483a0, #31b2c3);
  background-image: -moz-linear-gradient(top, #0483a0, #31b2c3);
  background-image: -ms-linear-gradient(top, #0483a0, #31b2c3);
  background-image: -o-linear-gradient(top, #0483a0, #31b2c3);
  background-image: linear-gradient(top, #0483a0, #31b2c3);
}

.form-wrapper #submit:active {
  -webkit-box-shadow: 0 1px 4px rgba(0, 0, 0, 0.5) inset;
  -moz-box-shadow: 0 1px 4px rgba(0, 0, 0, 0.5) inset;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.5) inset;
  outline: 0;
}

.form-wrapper #submit::-moz-focus-inner {
  border: 0;
}

